___CSS_LOADER_EXPORT___.push([module.id, `body {
  background-color: rgba(27, 122, 224, 0.619);
}

.title {
  text-align: center;
}

.container {
  width: 80vw;
  height: 10vh;
  margin: 2% auto;
}

.buttonList {
  cursor: pointer;
  padding: 1%;
  border-radius: 7px;
  margin: 1%;
  transition: all 0.3s ease-in-out;
  width: 80%;
}

.buttonList:hover {
  transform: scale(0.9);
}

.item {
  list-style: none;
  border: 1px solid bisque;
  width: 70%;
  margin: 1% auto;
  border-radius: 20px;
  display: flex;
}

.label-item {
  font-size: 1.2vw;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-around;
}

.label-item:nth-child(2) {
  flex-grow: 2;
}

.label-item:last-child {
  flex-grow: 0;
}

.input-item,
.button-item {
  margin: 2vw;
  border-radius: 10px;
}

.button-item {
  cursor: pointer;
  flex-grow: 0;
}
